import React from "react";

const Services = ({ classicHeader, darkTheme }) => {
  const skills = ["python", "javascript", "react", "relay", "graphql", "redux", "postgres",
    "node", "aws", "flask", "express", "sequelize", "sqlalchemy", "docker",
    "linux", "ubuntu", "gunicorn", "playwright", "your-stack"];

  return (
    <section
      id="services"
      className={"section " + (darkTheme ? "bg-dark-1" : "bg-light")}
    >
      <div className={"container " + (classicHeader ? "" : "px-lg-5")}>
        {/* Heading */}
        <div className="position-relative d-flex text-center mb-5">
          <h2
            className={
              "text-24  text-uppercase fw-600 w-100 mb-0 " +
              (darkTheme ? "text-white-50  opacity-1" : "text-light  opacity-4")
            }
          >
            Skills
          </h2>
          <p
            className={
              "text-9  fw-600 position-absolute w-100 align-self-center lh-base mb-0 " +
              (darkTheme ? "text-white" : "text-dark")
            }
          >
            What I work with
            <span className="heading-separator-line border-bottom border-3 border-primary d-block mx-auto" />
          </p>
        </div>
        {/* Heading end*/}
        {/* content start */}
        <div className="row">
          <div className="col-lg-11 mx-auto">
            <div className="skills-flex">
              {skills.map((skill, i) => (
                <i className={skill} key={i} />
              ))}
            </div>
          </div>
        </div>
        {/* content end */}
      </div>
    </section>
  );
};

export default Services;
